<template>
  <div :style="{display: getPopupType ? 'block' : 'none'}">
    <div id="shell" class='level6' @mouseover="timeout(false)" @mouseout="timeout(true)">
        <div id="body" class='level4' :class="['{{getVariantIdx < 0}}' ? '' : 'variantPopup']">
            <div id="header" @mousedown="dragMouseDown($event)" v-if="header">
                <span id="loadedPin" class='loadedPin' v-on:click='togglePinBehaviour()'></span>
                <span v-html="header"></span>
                <span class='x' id='x_close' @click="closePopup()"></span>
            </div>
            <div id="header2" @mousedown="dragMouseDown($event)" v-if="header2">
                <span id="loadedPin" class='loadedPin' v-on:click='togglePinBehaviour()'></span>
                <span v-html="header2"></span>
                <span class='x' id='x_close' @click="closePopup()"></span>
                <span class='helpAllVar' id='helpAllVar' @click="displayHelp()"></span>
            </div>
            <div id="content">
                <slot></slot>
            </div>
        </div>
      <TreeView v-show="showTree" id='treeView' class='level5' v-bind:treeReady='prop' :count='count' :coordinates="treeCoordinates" :parentElem="parentElem"/>
      <NodeLoader v-show="showLoader" :coordinates="treeCoordinates" :change='change'/>
      <svg :height="this.tailHeight" :width="this.tailWidth" id='tail' :style="{visibility: !float ? 'visible' : 'hidden'}">
        <polygon points="0 0, 7.5 50, 15 0, 15 0"></polygon>
      </svg>
    </div>
  </div>
</template>

<script>
import TreeView from '../MatchingStructures/TreeView'
import NodeLoader from '../AquariaLayout/NodeLoader'
import store from '../../store/index'
import { mapGetters } from 'vuex'
import axios from 'axios'
import * as panel3d from '../StructureViewer/helpers/joleculeAPI'

export default {
  name: 'PopupGeneric',
  components: {
    TreeView,
    NodeLoader
  },
  props: ['header2', 'header', 'PopupPos', 'parentRef'],
  data () {
    return {
      treeCoordinates: {},
      change: 0,
      parentElem: null,
      showTree: false,
      count: 0,
      isShowTreeViewClicked: false,
      prop: null,
      showLoader: false,
      tailWidth: 15,
      tailHeight: 50,
      pos: [0, 0, 0, 0],
      tailPosition: {},
      float: false // popup body floating without a tail
    }
  },
  computed: {
    ...mapGetters([
      'getVariant',
      'getVariantStructs',
      'getPopupText',
      'getVariantIdx',
      'getStructure',
      'getGene',
      'getPopupTitle',
      'getPopupType'
    ])
  },
  methods: {
    setPopupPos () {
      var pbody = document.querySelector('#' + this.parentRef + ' #body')
      var pshell = document.querySelector('#' + this.parentRef + ' #shell')
      var ptail = document.querySelector('#' + this.parentRef + ' #tail')
      if (ptail && pshell && pbody) {
        this.setTailPosition(ptail)
        this.setShellPosition(pshell)
        this.handleEdgeCases(pshell, pbody, ptail)
      }
    },
    setTailPosition: function (ptail) {
      const pos = this.PopupPos
      this.tailPosition = pos.x - (this.tailWidth / 2) + (pos.width / 2)
      ptail.style.left = this.tailPosition + 'px'
    },
    setShellPosition: function (pshell) {
      const pos = this.PopupPos
      if (typeof (this.getPopupText) !== 'object' && this.getPopupText.includes('Dark')) { // position popup tail on the line
        pos.y = this.PopupPos + 20
      }
      if (pos.height >= 100) {
        pshell.style.top = (pos.y - 2 - pos.height - this.tailHeight / 2) + 'px'
      } else if (pos.height > 20) { // This is for popup position in dark regions
        pshell.style.top = (pos.y - 2 - this.tailHeight - 10) + 'px'
      } else {
        pshell.style.top = (pos.y - 2 - this.tailHeight) + 'px'
      }
      if (this.parentRef === 'allVariants') {
        pshell.style.top = 30 + 'px'
        // console.log('updated the positions')
        // console.log(pos)
      }
      pshell.style.left = (this.tailPosition - 10) + 'px'
    },
    handleEdgeCases: function (pshell, pbody, ptail) {
      /** Handle right edge cases */
      var bodyPos = pshell.getBoundingClientRect().x + pbody.getBoundingClientRect().width
      if ((bodyPos) > window.innerWidth) {
        // Shift shell to the left. Add 10px to offset any gaps.
        pshell.style.left = (window.innerWidth - pbody.getBoundingClientRect().width + 7) + 'px'
      }
      /** Handle left edge cases */
      if (pbody.getBoundingClientRect().x < 0) {
        pshell.style.left = '-10px'
      }
      /** Handle top edge cases */
      if (pbody.getBoundingClientRect().y < 0) {
        if (!ptail.classList.contains('topTail')) {
          ptail.classList += ' topTail'
        }
        pbody.style.top = ptail.getBoundingClientRect().height - 2 + 'px'
      } else {
        if (ptail.classList.contains('topTail') && (ptail.getBoundingClientRect().y - pbody.getBoundingClientRect().height > 0)) {
          ptail.classList.remove('topTail')
          pbody.style.top = null
        }
      }
    },
    timeout: function (start) {
      var _this = this
      if (start && !this.$parent.pinned && !this.isShowTreeViewClicked) {
        _this.$parent.timer = _this.setTimer()
      } else if (this.$parent.timer) {
        clearTimeout(_this.$parent.timer)
      }
    },
    setTimer () {
      var _this = this
      function run () {
        _this.$parent.PopupPos = null
      }
      return setTimeout(run, 2000)
    },
    // The code below uses examples from https://www.w3schools.com/howto/howto_js_draggable.asp
    // Used to make the popup body draggable
    dragMouseDown: function (e) {
      e = e || window.event
      e.preventDefault()
      // get the mouse cursor position at startup:
      this.pos[2] = e.clientX
      this.pos[3] = e.clientY
      document.onmouseup = this.closeDragElement
      // call a function whenever the cursor moves:
      document.onmousemove = this.elementDrag
    },
    elementDrag: function (e) {
      e = e || window.event
      e.preventDefault()
      var elmnt = document.querySelector('#' + this.parentRef + ' #shell')
      // calculate the new cursor position:
      this.pos[0] = this.pos[2] - e.clientX
      this.pos[1] = this.pos[3] - e.clientY
      this.pos[2] = e.clientX
      this.pos[3] = e.clientY
      // set the element's new position:
      elmnt.style.top = (elmnt.offsetTop - this.pos[1]) + 'px'
      elmnt.style.left = (elmnt.offsetLeft - this.pos[0]) + 'px'
      this.float = true
    },
    closeDragElement: function () {
      // stop moving when mouse button is released:
      document.onmouseup = null
      document.onmousemove = null
    },
    togglePinBehaviour: function () {
      // 1. Get pin status.
      const thePin = document.querySelector('#' + this.parentRef + ' #loadedPin') // getElementById('loadedPin')
      if (thePin.classList.contains('loadedPin_active')) {
        thePin.classList.remove('loadedPin_active')
        thePin.classList.add('loadedPin')
        this.$parent.pinned = false
      } else if (thePin.classList.contains('loadedPin')) {
        this.timeout(false)
        this.$parent.pinned = true
        thePin.classList.remove('loadedPin')
        thePin.classList.add('loadedPin_active')
      }
    },
    setDefault: function () {
      // Sets default popup style
      const thePin = document.getElementById('loadedPin')
      if (thePin) {
        if (thePin.classList.contains('loadedPin_active')) {
          thePin.classList.remove('loadedPin_active')
          thePin.classList.add('loadedPin')
          this.$parent.pinned = false
        }
        const tail = document.getElementById('tail')
        if (tail.classList.contains('hide')) {
          tail.classList.remove('hide')
          tail.classList.add('show')
        }
        document.getElementById('shell').classList.remove('fade')
      }
    },
    closePopup () {
      if (!this.isShowTreeViewClicked) {
        if (this.parentRef === 'variant') {
          store.commit('setVariantCoordinates', null)
        } else if (this.parentRef === 'allVariants') {
          store.commit('setAllVariantsCoordinates', null)
        }
        this.$parent.PopupPos = null
      } else {
        this.isShowTreeViewClicked = false
      }
    },
    showTreeView (event, aaCode, residue, listOfPdbs, sortedKeys, count) {
      var _this = this
      this.timeout(false)
      this.isShowTreeViewClicked = true
      var cord = event.srcElement.getBoundingClientRect()
      this.treeCoordinates.clientX = cord.left
      this.treeCoordinates.clientY = cord.top
      this.treeCoordinates.width = cord.width
      this.treeCoordinates.height = cord.height
      this.change = _this.change + 1
      this.showLoader = true
      var url
      var loadRequest
      if (this.$el.id === 'allVariants') {
        console.log('Calling allVariants')
        console.log(listOfPdbs)
        this.parentElem = '#allVariants #shell'
        this.count = count

        url = `${window.BACKEND}/getSecondaryClustersfromPDB2`

        loadRequest = {}
        loadRequest.pdbs = []
        loadRequest.sortedKeys = []

        loadRequest.selector = [_this.getStructure.accession]
        listOfPdbs.forEach(function (pdb, pdbI) {
          loadRequest.pdbs.push(pdb)
        })

        sortedKeys.forEach(function (aKey, aKeyI) {
          loadRequest.sortedKeys.push(aKey)
        })

        console.log(loadRequest)

        axios({
          method: 'get',
          url: url,
          params: loadRequest
        }).then(function (response) {
          console.log('Recieved the response')
          console.log(response.data)
          // pdbs = response.data
          _this.showTree = true
          _this.prop = response.data
        })
      } else if (this.$el.id === 'variant') {
      // if (this.getPopupTitle && this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant)) {
        this.parentElem = '#variant #shell'
        if (aaCode) {
          /** @todo
           * bring getPDBCount to this component
           */
          _this.count = _this.getPdbCount(aaCode)
        } else {
          _this.count = _this.getTotalPdbCount
        }

        url = `${window.BACKEND}/secondaryClusteringByResidue`
        // var url = `${window.BACKEND}/get_secondary_clusters/P16277/5zj6/A/1`
        loadRequest = {}
        loadRequest.selector = [_this.getStructure.accession]
        loadRequest.sortedKeys = residue ? [parseInt(residue)] : [_this.getVariant]
        if (aaCode) {
          loadRequest.aaCode = aaCode
        }
        axios({
          method: 'get',
          url: url,
          params: loadRequest
        }).then(function (response) {
          console.log(response.data)
          // pdbs = response.data
          _this.showTree = true
          _this.prop = response.data
        })
      } else {
        this.parentElem = '#minimal #shell'
        url = `${window.BACKEND}/secondaryClusteringByResidue`
        // var url = `${window.BACKEND}/get_secondary_clusters/P16277/5zj6/A/1`
        _this.count = _this.getTotalPdbCount
        loadRequest = {}
        loadRequest.selector = [_this.getStructure.accession]
        loadRequest.sortedKeys = [parseInt(_this.getPopupText.match(/ \d*:/)[0])]
        axios({
          method: 'get',
          url: url,
          params: loadRequest
        }).then(function (response) {
          console.log(response.data)
          // pdbs = response.data
          _this.showTree = true
          _this.prop = response.data
        })
      }
      return 0
    },
    getPdbCount: function (aa) {
      // console.log(store.getters.getVariant)
      // return 100// store.getters.getPdbCount(aa)
      if (this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], aa) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant][aa], 'pdbs')) {
        return this.getVariantStructs[this.getVariant][aa].pdbs.length
      }
      return 0
    },
    getResInfo: function () {
      var jAlignment = panel3d.joleculeAlignment
      var jController = panel3d.embededJolecule.controller
      var pdbResNum = jAlignment.mapSeqResToPdbResOfChain(0, this.getVariant, this.getStructure.chain)
      if (pdbResNum) {
        var iRes = jController.soup.findResidueIndices(0, pdbResNum[1], pdbResNum[2])
        var structAa = jController.soup.getResidueProxy().load(iRes).resType
        structAa = structAa.substr(0, 1) + structAa.substr(1).toLowerCase()
        return this.getGene + ' residue ' + this.getVariant + '<span style="font-weight: 400; color: #a6a5a6;"> (' + this.getStructure.pdb + '-' + this.getStructure.chain + ' residue ' + structAa + '-' + pdbResNum[2] + ')'
      } else if (document.querySelector('[id^="residue_' + this.getVariant)) {
        return this.getGene + ' residue ' + this.getVariant + '<span style="font-weight: 400; color: #a6a5a6;">'
      } else {
        return this.getGene + ' residue ' + this.getVariant + '<span style="font-weight: 400; color: #a6a5a6;"> ( Dark region )'
      }
    },
    displayHelp () {
      // var _this = this
      const theHelpBtn = document.getElementById('helpAllVar')
      if (theHelpBtn.classList.contains('helpAllVar_active')) {
        theHelpBtn.classList.add('helpAllVar')
        theHelpBtn.classList.remove('helpAllVar_active')
        document.getElementById('theHelpContent').style.visibility = 'hidden'
        document.getElementById('theHelpContent').style.height = '0px'
      } else {
        theHelpBtn.classList.add('helpAllVar_active')
        theHelpBtn.classList.remove('helpAllVar')
        document.getElementById('theHelpContent').style.visibility = 'visible'
        document.getElementById('theHelpContent').style.height = 'auto'
      }
    }
  },
  updated () {
    // Set popup position after popup body updated
    if (this.PopupPos && this.PopupPos !== null) {
      this.setPopupPos(this.PopupPos)
    }
  }
}
</script>

