<template>
  <div class="main">
    <GdprConsentModal />
    <Header :showHelpIcon='showHelpIcon'/>
    <Sequence v-if="!pdbOnly" :showSequencePanel="showSequenceInfo"/>
    <AboutAlignment v-if="!pdbOnly" :showAlignmentPanel="showAlignmentInfo"/>
    <AboutPdb v-bind:showPDBPanel="showStructureInfo"/>
    <div>
      <AboutAquaria :showPanel="showAboutAquaria"/>
      <UserHelp />
      <Errors />
    </div>
    <div id="heightinspector"></div>
    <StructureViewer />
    <Tabs :style="{visibility: !pdbOnly ? 'visible' : 'hidden'}"/>
    <CoverageMap v-if="!pdbOnly"/>
    <PopUpVariant />
    <PopupMinimal />
    <PopupAllVar />
    <Scrim :show="showScrim"/>
    <!-- <PopupAllVarSummary /> -->
  </div>
</template>

<script>
import store from '../store/index'
import '../components/AquariaLayout/helpers/aquaria' // most legacy code bundling is triggered from here (expects global jolecule)
import Header from '../components/Header/Header'
import Scrim from '../components/AquariaLayout/Scrim.vue'
import Sequence from '../components/InfoAbout/Sequence'
import AboutAlignment from '../components/InfoAbout/AboutAlignment'
import AboutPdb from '../components/InfoAbout/AboutPdb'
import StructureViewer from '../components/StructureViewer/StructureViewer'
import Tabs from '../components/AquariaLayout/Tabs'
import CoverageMap from '../components/MatchingStructures/CoverageMap'
import AboutAquaria from '../components/InfoAbout/AboutAquaria'
import Errors from '../components/InfoAbout/Errors'
import UserHelp from '../components/InfoAbout/UserHelp'
import * as LoadAQUARIA from '../utils/loadData'
import PopUpVariant from '../components/Popup/PopUpVariant.vue'
import PopupMinimal from '../components/Popup/PopupMinimal.vue'
// import PopupAllVarSummary from '../components/Popup/PopupAllVarSummary.vue'
import GdprConsentModal from '../views/GdprConsent.vue'
import * as showMatchingStructures from '../components/MatchingStructures/helpers/show_matching_structures'
import * as panel3d from '../components/StructureViewer/helpers/joleculeAPI'
import * as View from '../components/InfoAbout/helpers/ViewUtils'
import PopupAllVar from '../components/Popup/PopupAllVar.vue'

export default {
  name: 'Wrapper',
  components: {
    GdprConsentModal,
    Header,
    Sequence,
    AboutAlignment,
    AboutPdb,
    StructureViewer,
    Tabs,
    CoverageMap,
    PopUpVariant,
    PopupMinimal,
    // PopupAllVarSummary,
    AboutAquaria,
    Errors,
    UserHelp,
    Scrim,
    PopupAllVar
  },
  props: ['urlRequest'],
  data () {
    return {
      pdbOnly: false,
      showSequenceInfo: false,
      showAlignmentInfo: false,
      showStructureInfo: false,
      showHelpIcon: true,
      showAboutAquaria: false
    }
  },
  computed: {
    data () {
      return {
        hash: store.state.hash,
        arrayOfVideos: [
          'https://youtu.be/FAQ3yVGYSzY?list=PLsGaleFn8YydVFkxDhOvHHE5NoTsl6D1e',
          'https://www.youtube.com/watch?v=UZNEmPOaVrA&list=PLsGaleFn8YydVFkxDhOvHHE5NoTsl6D1e&index=2'
        ]
      }
    },
    hash () {
      return store.state.hash
    },
    residueSelected () {
      return store.state.residueSelected
    },
    accession () {
      return store.state.structure.accession
    },
    showScrim () {
      return store.state.showScrim
    }
  },
  watch: {
    hash (fragment) {
      var cleaned = decodeURIComponent(window.location.search)
      history.replaceState(null, null, `${window.location.origin}${window.location.pathname}${cleaned}#${fragment}`)
    },
    residueSelected (value) {
      var _this = this
      document.querySelector('#toggle-toolbar-button').style.display = 'none'
      if (!value) {
        _this.showInitialState()
      } else {
        _this.showSelectedState()
      }
    },
    accession (value) {
      var _this = this
      if (!value) {
        value = ''
      }
      if (value.length > 0) {
        _this.pdbOnly = false
      } else {
        _this.pdbOnly = true
      }
    }
  },
  methods: {
    resetPopup: function () {
      this.$store.commit('setPopupType', null)
    },
    appendHash: function (hash) {
      // Store fragment value before it gets replaced
      if (window.location.hash === '' || (!window.location.hash.includes('(') || !window.location.hash.includes('%'))) {
        store.commit('setHash', hash)
      }
      if (window.location.hash.includes('(') || window.location.hash.includes('%') || window.location.hash.match(/[A-Z]?[0-9]/)) {
        let oldHash = window.location.hash.split('#')[1]
        if (oldHash.split('&').length > 1) {
          oldHash = oldHash.includes('%') ? oldHash.split('%')[0] + '%' : (oldHash.includes(')') ? oldHash.split(')')[0] + ')' : '')
        } else {
          oldHash = oldHash.split('&')[0]
        }
        store.commit('setHash', oldHash + '&' + hash)
      }
    },
    dropFragment: function () {
      if (window.location.hash.length > 0) { // Continue if a fragment present
        if (!(window.location.hash.includes('(') || window.location.hash.includes('%') || window.location.hash.match(/[A-Z]?[0-9]/))) {
          store.commit('setHash', '')
        } else {
          let newHash = ''
          const oldHash = window.location.hash.split('#')[1]
          if (oldHash.split('&').length > 1) {
            newHash = oldHash.includes('%') ? oldHash.split('%')[0] + '%' : (oldHash.includes(')') ? (oldHash.split(')')[0] + ')') : '')
            newHash = (newHash.length < 1 && oldHash.match(/[A-Z]?[0-9]/)) ? oldHash.split('&')[0] : newHash
          } else {
            newHash = oldHash.split(')')[0] + ')'
          }
          store.commit('setHash', newHash)
        }
      }
    },
    showInitialState: function () {
      document.querySelector('.jolecule-embed-footer').style.visibility = 'hidden'
      document.querySelector('#color-legend').style.visibility = 'hidden'
      document.querySelector('#color-legend').style.bottom = '105px'
      document.querySelector('#threeDSpan-inner').style.height = '100%'
      if (document.querySelector('#coverageMap')) {
        document.querySelector('#coverageMap').style.display = 'block'
      }
      if (document.querySelector('#map')) {
        document.querySelector('#map').style.visibility = 'visible'
      }
      if (document.querySelector('div.tabs')) {
        document.querySelector('div.tabs').style = ''
      }
    },
    showSelectedState: function () {
      document.querySelector('.jolecule-embed-footer').style.display = 'flex'
      document.querySelector('.jolecule-embed-footer').style.visibility = 'visible'
      document.querySelector('#color-legend').style.bottom = '105px'
      document.querySelector('#color-legend').style.visibility = 'visible'
      document.querySelector('#threeDSpan-inner').style.height = '100%'
      if (document.querySelector('#coverageMap')) {
        document.querySelector('#coverageMap').style.display = 'none'
      }
      if (document.querySelector('#map')) {
        document.querySelector('#map').style.visibility = 'hidden'
      }
      if (document.querySelector('div.tabs')) {
        document.querySelector('div.tabs').style.top = '-1.1rem'
      }
      document.querySelector('#threeDSpan-inner-jolecule-soup-display-canvas-wrapper-selection').style.bottom = '105px'
      var popupTail = document.getElementById('popupTail')
      if (popupTail.classList.contains('show')) {
        popupTail.classList.remove('show')
        popupTail.classList.add('hide')
      }
      var popupTail2 = document.getElementById('popupTail2')
      if (popupTail2.classList.contains('show')) {
        popupTail2.classList.remove('show')
        popupTail2.classList.add('hide')
      }
    },
    checkPhone: function () {
      var iDevices = [
        'iPad Simulator',
        'iPhone Simulator',
        'iPod Simulator',
        'iPad',
        'iPhone',
        'iPod'
      ]

      if (navigator.platform) {
        while (iDevices.length) {
          if (navigator.platform === iDevices.pop() || (screen.width >= 300 && screen.width <= 600)) { return true }
        }
      } else {
        return false
      }
    },
    inspectHeight: function () {
      // var visible = window.toolbar.visible
      document.querySelector('#heightinspector').innerHTML = 'reset'
      var hDiff = (window.outerHeight - window.innerHeight)
      document.querySelector('#heightinspector').innerHTML = 'HO:' + window.outerHeight + ', hDiff:' + hDiff + ', scroll: ' + window.scrollY + ' bodyHeight: ' + document.body.scrollHeight
    },
    // dismissPanel: function () {
    //   // hide scrim
    //   document.querySelector('#scrim').className = 'hide'
    //   // reset title to neutral state
    //   this.$store.commit('setScrim', false)
    // },
    toggleActive: function (ev) {
      if (ev.className === 'lnk active') {
        ev.className = 'lnk'
      }
    },
    loadAccession () {
      this.showInitialState()
      LoadAQUARIA.loadAccession([this.$props.urlRequest.uniprotAccession], this.$props.urlRequest.pdbID, this.$props.urlRequest.chain)
    },
    loadPDB () {
      this.pdbOnly = true
      store.commit('setResidueSelected', false)
      LoadAQUARIA.loadPDB(this.$props.urlRequest.pdbID)
    }
  },
  mounted () {
    // Functions to run immediately after home page DOM first finishes loading.
    var _this = this
    const hash = window.location.hash.split('#')[1]
    if (hash) {
      store.commit('setHash', hash)
    } else {
      store.commit('setHash', '')
    }
    /** Initialise Jolecule panel */
    panel3d.initialisePanel3d()

    /** Load State  */
    if (this.$props.urlRequest.uniprotAccession) {
      _this.loadAccession()
    } else {
      _this.loadPDB()
    }

    /** Watch for Jolecule change */
    const joleculeWatcher = new View.JoleculeWatcher(panel3d.embededJolecule)
    // update URL with view information upon 'view commit' events
    // note: selection changes assumed to always also trigger an animation-driven viewcommit as Jolecule updates the view based on the new selection
    joleculeWatcher.addEventListener('viewcommit', () => View.saveToUrl())

    /** On resize update panels */
    window.addEventListener('resize', function () {
      window.scrollBy(0, 100)
      document.querySelectorAll('#vis > .ruler').forEach(el => el.remove())
      document.querySelectorAll('#vis > #allclusters .outer_container').forEach(el => el.remove())
      showMatchingStructures.refresh()
      var hDiff = (window.outerHeight - window.innerHeight)
      document.querySelector('#heightinspector').innerHTML = 'HO:' + window.outerHeight + ', hDiff:' + hDiff + ', scroll: ' + window.scrollY + ' bodyHeight: ' + document.body.scrollHeight
    })

    /** COVID resource Scrim */
    setTimeout(function () {
      if (document.querySelector('.matrixLoading')) {
        document.querySelector('.matrixLoading').style.visibility = 'hidden'
        document.querySelector('.matrixLoading').style.display = 'none'
      }
    }, 1500)
  },
  updated () {
    const isPhone = this.checkPhone()
    var hDiff = (window.outerHeight - window.innerHeight)
    if (isPhone && window.innerHeight > 315) {
      if (Math.abs(hDiff) >= 114) {
        window.scrollTo(0, 0)
      } else {
        window.scrollTo(0, 9)
      }
    }
  }
}
</script>

