<template>
  <div id="structure-viewer">
    <span id="threeDSpan">
      <div id="threeDSpan-inner"></div>
    </span>
  </div>
</template>

<script>
import * as panel3d from './helpers/joleculeAPI'
import { lerpFrequency } from './helpers/timers'

// total time for fade animation (ms)
const SELECTION_FADE_DURATION_MS = 2000
// interval between fade increments (ms)
const SELECTON_FADE_INTERVAL_MS = 100
// default selection lightness offset (0.3 is jolecule standard)
const DEFAULT_SELECTION_LIGHTNESS_OFFSET = 0.3

export default {
  name: 'StructureViewer',
  data () {
    return {
      timer: null
    }
  },
  computed: {
    selectedResidues () {
      return this.$store.state.selectedResidues
    }
  },
  // computed: {
  //   hash () {
  //     return store.state.hash
  //   }
  // },
  watch: {
    selectedResidues (newVal) {
      if (newVal.length > 0) {
        this.hideSelection()
      }
    }
  },
  // @TODO On residue click, if new chain selected, remove sequence bar and diselect
  // computed: {
  //   sequence () {
  //     return store.state.structure
  //   }
  // },
  // watch: {
  //   sequence (newVal, oldVal) {
  //     if (oldVal.accession !== newVal.accession && oldVal.accession !== null) { // Only redraw coverage map if new chain
  //       window.scrollBy(0, 100)
  //       document.querySelector('#vis > .ruler').remove()
  //       document.querySelectorAll('#vis > #allclusters .outer_container').forEach(el => el.remove())
  //       window.AQUARIA.refresh() // This redraws matching structures panel
  //       var hDiff = (window.outerHeight - window.innerHeight)
  //       document.querySelector('#heightinspector').innerHTML = 'HO:' + window.outerHeight + ', hDiff:' + hDiff + ', scroll: ' + window.scrollY + ' bodyHeight: ' + document.body.scrollHeight
  //     }
  //   }
  // },
  mounted () {
    // don't show selection on mousedown in case it is a click that would change residue, causing a flash of the old
    // residue before selection is complete. only show selection if this is the first mousemove after a mousedown
    let mousedown = false
    window.addEventListener('mousedown', () => {
      mousedown = true
    })
    window.addEventListener('mousemove', () => {
      if (mousedown) this.showSelection()
      mousedown = false
    })
    window.addEventListener('mouseup', () => {
      mousedown = false
      this.hideSelection()
    })
    window.addEventListener('gesturestart', () => this.showSelection()) // always show selection
    window.addEventListener('gestureend', () => this.hideSelection()) // possibly hide selection
    window.addEventListener('gesturechange', () => this.showSelection())

    if (this.checkPhone()) {
      this.adjustSize()
    }
  },
  methods: {
    showSelection: function () {
      this.selectionTimeout(false)
    },
    hideSelection: function () {
      this.selectionTimeout(true)
    },
    setTimer () {
      panel3d.setSelectionLightnessOffset(DEFAULT_SELECTION_LIGHTNESS_OFFSET) // set default selection lightness value

      // lerp from default to zero (hidden selection)
      var timer = lerpFrequency(DEFAULT_SELECTION_LIGHTNESS_OFFSET, 0, SELECTION_FADE_DURATION_MS, SELECTON_FADE_INTERVAL_MS, (value, finished) => {
        panel3d.setSelectionLightnessOffset(value)
      }, SELECTON_FADE_INTERVAL_MS)
      return timer
    },
    selectionTimeout: function (start) {
      // always clear any existing timer
      if (this.timer) clearTimeout(this.timer)
      // start a new fade if requested
      if (start) this.timer = this.setTimer()
      // else set the selection highlight to the default value (cancel out the effects of any in-progress fade)
      else panel3d.setSelectionLightnessOffset(DEFAULT_SELECTION_LIGHTNESS_OFFSET)
    },
    checkPhone: function () {
      var iDevices = [
        'iPad Simulator',
        'iPhone Simulator',
        'iPod Simulator',
        'iPad',
        'iPhone',
        'iPod'
      ]

      if (navigator.platform) {
        while (iDevices.length) {
          if (navigator.platform === iDevices.pop() || (screen.width >= 300 && screen.width <= 600)) { return true }
        }
      } else {
        return false
      }
    },
    adjustSize: function () {
      const baseWindowHeight = Math.max(window.innerHeight)
      const documentBody = document.body
      document.addEventListener('scroll', function (e) {
        var newWindowHeight = Math.max(window.innerHeight)
        if (newWindowHeight - 50 > baseWindowHeight) {
          if (!documentBody.classList.contains('ios-toolbar')) {
            documentBody.classList.add('ios-toolbar')
          }
        } else {
          if (documentBody.classList.contains('ios-toolbar')) {
            documentBody.classList.remove('ios-toolbar')
          }
        }
      })
    }
  }
}
</script>

