<template>
    <div class="panel" id="ftPanel" v-if="showPanel">
        <div class="close" @click="closeTab"></div>
        <div id="featurePanel">
          <div id="groupedFeatures" class="ui-sortable">
            <!-- <AddedFeatures/> -->
            <div :id="structure[0]" class="featureCollection ui-sortable-handle" v-for="structure in getfeatureSets" :key="structure[0]" @click="toggleTrack(structure[0])" >
              <div class="featureHeader" @mouseover="highlight(structure[0])" @mouseout="removeHighlight(structure[0])">
                <p id="arrow">►</p>
                <p class="featureName">{{ structure[0] }}</p>
                <div class="info" @mouseover="showInfo(structure, $event)">?</div>
              </div>
              <div class="featureTrack ui-sortable"></div>
            </div>
          </div>
          <br/>
        </div>
    </div>
</template>
<script>
// import AddedFeatures from './AddedFeatures.vue'
import * as Panels from '../AquariaLayout/helpers/hidePanels'
import store from '../../store/index'
import * as loadFeature from './helpers/loadFeature'
import { mapGetters } from 'vuex'
import * as fetchAnnotations from './helpers/fetch_features'
import _ from 'lodash'
import * as panel3d from '../StructureViewer/helpers/joleculeAPI'
import * as viewUtils from '../InfoAbout/helpers/ViewUtils'
import * as renderFeature from './helpers/renderPanel'
import * as showMatchingStructures from '../MatchingStructures/helpers/show_matching_structures'

export default {
  name: 'Features',
  props: ['showPanel'],
  data () {
    return {
      jAlignment: null,
      jController: null,
      accession: '',
      initialFeature: false
    }
  },
  // components: {
  //   AddedFeatures
  // },
  computed: {
    Loader () {
      return store.state.Loader
    },
    sideChain () {
      return store.state.sidechain
    },
    ...mapGetters([
      'getfeatureSets',
      'getcompleteServerNum',
      'getLoadedFeature',
      'getCoverageMap',
      'getPdbIndex',
      'getStructure'
    ])
  },
  watch: {
    /**
     * When complete server object updated using newly fetched features, update feature panel
     */
    showPanel (newVal) {
      console.log(newVal)
    },
    getcompleteServerNum: function (newVal, oldVal) {
      var _this = this
      var featureInfo = this.$store.state.aggregatedAnnotations
      var completeServers = this.$store.state.completeExtServers
      if (newVal !== oldVal) {
        _this.groupFeatureSets(featureInfo, completeServers)
        if (_this.getCoverageMap && Object.keys(_this.$route.query).length > 0) {
          _this.checkAddedFeature()
        }
      }
    },
    getPdbIndex: function (newVal) {
      if (newVal.cluster !== null && !this.Loader) {
        this.initiate()
      }
    },
    Loader: function (newVal) {
      var _this = this
      if (!newVal) {
        if (_this.$store.state.structure.accession !== _this.accession) {
          _this.accession = _this.$store.state.structure.accession
          _this.fetch_features(this.$store.state.Matches.sequences)
        }
        if (Object.keys(_this.$route.query).length > 0 || _this.getLoadedFeature.set) {
          store.commit('setLoadedFeature', { set: _this.getLoadedFeature.set, track: 0 })
        }
        window.addEventListener('hashchange', e => _this.updateSelection(window.location.hash), {})
      }
    },
    getLoadedFeature: function (data, old) {
      var _this = this
      if ((data.set !== old.set || !old.set) && data.set) {
        _this.initialFeature = false
        loadFeature.loadFeature(data.set, data.track)
        if (this.sideChain !== null) {
          loadFeature.showSideChains(this.sideChain)
        }
      } else if (data.set === old.set && data.set && !this.initialFeature) {
        loadFeature.removeFeature()
      }
      // } else if (data.set === old.set && data.set) {
      //   store.commit('setLoadedFeature', { set: null, track: null })
      // }
      // this.$parent.$parent.showScrim = false
    }
  },
  methods: {
    initiate: function () {
      var _this = this
      if (_this.$store.state.structure.accession !== _this.accession) {
        _this.accession = _this.$store.state.structure.accession
        store.commit('setfeatureSets', null)
        _this.fetch_features(this.$store.state.Matches.sequences)
      }
      if (Object.keys(_this.$route.query).length > 0 || _this.getLoadedFeature.set) {
        _this.initialFeature = true
        store.commit('setLoadedFeature', { set: _this.getLoadedFeature.set, track: 0 })
      }
      window.addEventListener('hashchange', e => _this.updateSelection(window.location.hash), {})
    },
    closeTab: function () {
      window.scrollBy(0, 100)
      Panels.hidePanels()
    },
    clearResidues: function () {
      panel3d.embededJolecule.controller.clearSelectedResidues()
    },
    toggleTrack: function (event) {
      const eventElem = document.getElementById(event)
      if (eventElem) {
        const trackElem = eventElem.lastChild
        event = event.replace(/\s/g, '\\ ')
        if (trackElem.style.display !== 'block') {
          trackElem.style.display = 'block'
          document.querySelector('#' + event + ' #arrow').innerText = '▼'
        } else {
          trackElem.style.display = 'none'
          document.querySelector('#' + event + ' #arrow').innerText = '►'
        }
      }
    },
    highlight: function (id) {
      document.querySelector('#' + id.replace(/\s/g, '\\ ') + ' > .featureHeader').style.background = '#bebebe'
    },
    removeHighlight: function (id) {
      document.querySelector('#' + id.replace(/\s/g, '\\ ') + ' > .featureHeader').style.background = '#ccc'
    },
    showInfo: function (feature, event) {
      console.log('Some feature checking string ')
      console.log(feature)
      // store.commit('setPopupTitle', feature[0])
      var popupText = feature[1][0].About ? feature[1][0].About : 'No description available'
      store.commit('setTotalPDBres', 0)
      store.commit('setPopupText', popupText)
      store.commit('setIsDefaultFeature', true)
      store.commit('setPopupType', 'minimal')
      store.commit('setVariantIdx', -1)
      store.commit('setPopupTitle', '')
      // store.commit('setPopupType', 'feature')
      store.commit('setPopupCoordinates', event.srcElement.getBoundingClientRect())
      store.commit('setPopupCoordinates2', event.srcElement.getBoundingClientRect())
    },
    fetch_features: async function (sequences) {
      var primaryAccession = []
      var uniprotMD5hash = []

      sequences.forEach(function (sequence) {
        primaryAccession.push(sequence.primary_accession)
        uniprotMD5hash.push(sequence.uniprot_hash)
      })
      await fetchAnnotations.fetch_annotations(primaryAccession)
    },
    groupFeatureSets: function (featureList, extServerIds) {
      if (featureList.length > 0) {
        var featureSet = featureList || featureSet
        var extServerIds_ = extServerIds || extServerIds_
        // Group by feature source
        var groupedFeatures = _.groupBy(featureSet, function (feature) {
          return feature.Server
        })
        // Store as an object
        groupedFeatures = Object.keys(groupedFeatures).map(function (key) {
          return [String(key), groupedFeatures[key]]
        })
        /**
         * Replace setfeatureSets by completeExtServers
         */
        if (store.getters.getfeatureSets === null) {
          store.commit('setfeatureSets', groupedFeatures)
        } else if (store.getters.getfeatureSets.length < groupedFeatures.length) {
          store.commit('setfeatureSets', groupedFeatures)
        }
      }
    },
    checkAddedFeature () {
      var _this = this
      var featureSets = this.$store.state.featureSets ? this.$store.state.featureSets : []
      if (featureSets.length > 0) {
        featureSets.forEach(function (f) {
          if (f[0].includes('Added') && _this.getLoadedFeature.set === null) {
            store.commit('setLoadedFeature', { set: f[1][0], track: 0 })
          }
        })
      }
    },
    updateSelection (hash) {
      viewUtils.loadFromUrl()
    },

    drawAxisRuler () {
      if (document.querySelector('#featurePanel > .ruler')) {
        document.querySelector('#featurePanel > .ruler').remove()
      }
      showMatchingStructures.drawAxisRuler('featurePanel')
    }
  },
  updated () {
    // @TODO Update features panel if sequence changes - updated
    showMatchingStructures.initialise(this.$store.state.Matches.sequences[0])
    if (this.getfeatureSets) {
      this.drawAxisRuler()
      renderFeature.updateFeatureUI(this.getfeatureSets)
    }
  }
}
</script>

