<template>
  <div>
    <div id="Sequence" class="panel overlay about level3">
      <SearchPanel />
      <AboutUniprot />
    </div>
  </div>
</template>
<script>
import SearchPanel from '../Search/SearchPanel'
import AboutUniprot from './AboutUniprot'
export default {
  name: 'Sequence',
  components: {
    SearchPanel,
    AboutUniprot
  },
  props: ['showSequencePanel'],
  watch: {
    showSequencePanel (bool) {
      if (bool) {
        document.querySelector('#Sequence').style.opacity = 1
        document.querySelector('#Sequence').style.display = 'flex'
      } else {
        document.querySelector('#Sequence').style.opacity = 0
        document.querySelector('#Sequence').style.display = 'none'
      }
    }
  }
}
</script>

<style scoped>
#Sequence {
  background-color: var(--primary-tab);
  opacity: 0;
  height: auto;
  order: 3;
  flex-direction: column;
  /* width: 100%; */
  transition: All 0.8s ease;
}
</style>
