<template>
    <PopupGeneric :key="'minimal'" id='minimal' :PopupPos="PopupPos" :parentRef="ref" :style="{display: (PopupPos && getPopupType) ? 'block' : 'none'}">
        <div class='minimal' v-if="PopupPos && getPopupType === 'minimal'">
          <b v-if="getPopupTitle != ''" v-html="getPopupTitle"></b>
          <br v-if="getPopupTitle">
          <i v-html="getPopupText"></i>
          <span :class="getTotalPDBres > 0 ? 'hasStruct' : 'noStruct'" @click="getTotalPDBres > 0 ? showTreeView($event) : ''"> {{ getTotalPDBres }} </span>
        </div>
    </PopupGeneric>
</template>

<script>
import PopupGeneric from './PopupGeneric.vue'
import { mapGetters } from 'vuex'
import store from '../../store/index'

export default {
  name: 'PopupMinimal',
  components: {
    PopupGeneric
  },
  data () {
    return {
      PopupPos: null,
      ref: 'minimal',
      timeOutVar: null,
      timer: null
    }
  },
  computed: {
    data () {
      return {
        variantStructs: store.state.variantStructs,
        variantResidues: store.state.variantResidues,
        popupTitle: store.state.popupTitle,
        popupText: store.state.popupText,
        residue: store.state.residue
      }
    },
    popupCoordinates () {
      return store.state.popupCoordinates
    },
    variantCoordinates () {
      return store.state.variantCoordinates
    },
    ...mapGetters([
      'getTotalPDBres',
      'getVariant',
      'getVariantStructs',
      'getPopupText',
      'getVariantIdx',
      'getIsDefaultFeature',
      'getPopupType',
      'getPopupTitle'
    ]),
    getTotalPdbCount () {
      return this.getTotalPDBres
    }
  },
  watch: {
    popupCoordinates: function (newVal) {
      if (newVal) {
        store.commit('setVariantCoordinates', null)
        this.PopupPos = newVal
        this.stoptimeout()
      } else {
        this.starttimeout()
      }
    },
    variantCoordinates: function (newVal) {
      if (newVal) {
        this.PopupPos = null
      }
    }
  },
  methods: {
    starttimeout: function () {
      this.$children[0].timeout(true)
    },
    stoptimeout: function () {
      this.$children[0].timeout(false)
    },
    getResInfo: function () {
      return this.$children[0].getResInfo()
    },
    showTreeView: function (event) {
      this.$children[0].showTreeView(event, null)
    }
  }
}
</script>

<style scoped>
.minimal {
    padding: 5px;
    cursor: pointer;
    color: var(--primary-label);
}
</style>
