/**
 * Linear interpolate one value to another over a given duration with a given interval frequency
 * @param {number} start start value
 * @param {number} end end value
 * @param {number} duration total duration of the lerp (ms)
 * @param {number} frequency time between intervals (ms)
 * @param {(value: number, finished: boolean) => void} callback
 * @returns {number} timer ID, which will be automatically cleared on completion or can be manually cleared with setInterval
 */
function lerpFrequency (start, end, duration, frequency, callback) {
    return lerpInternal(start, end, duration, frequency, callback)
}

/**
 * Linear interpolate one value to another over a given duration with a given number of intervals
 * @param {number} start start value
 * @param {number} end end value
 * @param {number} duration total duration of the lerp (ms)
 * @param {number} intervals number of intervals
 * @param {(value: number, finished: boolean) => void} callback
 * @returns {number} timer ID, which will be automatically cleared on completion or can be manually cleared with setInterval
 */
function lerpIntervals (start, end, duration, intervals, callback) {
    const frequency = duration / intervals
    return lerpInternal(start, end, duration, frequency, callback);
}

function lerpInternal (start, end, duration, frequency, callback) {
    const delta = (end - start) / (duration / frequency)
    let value = start
    const timer = setInterval(() => {
      value += delta
      let finished = false
      if (Math.abs(value - end <= Number.EPSILON)) {
        value = end // account for rounding errors
        clearInterval(timer)
        finished = true
      }
      callback(value, finished)
    }, frequency)
    return timer
}

export { lerpIntervals, lerpFrequency }