<template>
    <div id="title" class="item title fix level4">
      <!-- Only PDB -->
      <span id="pdbChain" v-if="!getStructure.accession">
        <span class="pdbPanel" @click="makeActive('Structure')">
          <img v-bind:src="search"> PDB <strong>{{getStructure.pdb}}</strong>
        </span>
        <span id='help' @click="showHelp" :style="{visibility: helpOption ? 'visible' : 'hidden'}">?</span>
      </span>

      <!-- Sequence aligned to PDB -->
      <span id="org_prot" v-if="getStructure.accession">
        <span class="Sequence" @click="makeActive('Sequence')" v-if="getOrganism && !seqRes" :style="{background: (activeTab === 'Sequence') ? '#ed9824' : 'none'}">
          <img v-bind:src="search">
          {{ getOrganism.replaceAll('_', ' ') }} <strong>{{getGene}}</strong>
        </span>
        <span class="Sequence" @click="makeActive('Sequence')" v-if="!getOrganism && !seqRes">
          <img v-bind:src="search">
          Protein Sequence
        </span>
      </span>
      <span id="alignment" v-if="!seqRes && getStructure.accession">
          <span class="alignPanel" @click="makeActive('Alignment')" :style="{background: (activeTab === 'Alignment') ? '#ed9824' : 'none'}">aligned onto</span>
      </span>
      <span id="struct" v-if="!seqRes && getStructure.accession"  @mouseover="showAllVarPopup()">
        <span href="#Structure" class="structPanel" @click="makeActive('Structure')" v-if="!getStructure.pdb">PDB ID</span>
        <span href="#Structure" class="structPanel" @click="makeActive('Structure')" v-if="getStructure.pdb && !seqRes" :style="{background: (activeTab === 'Structure') ? '#ed9824' : 'none'}">{{getStructure.pdb + '-' + getStructure.chain}}</span>
        <span id='help' @click="showHelp" :style="{visibility: helpOption ? 'visible' : 'hidden'}">?</span>
      </span>

      <!-- Selection Panel -->
      <span class="selectionPanel" @click="makeActive('Alignment')" v-if="seqRes && getStructure.accession" :style="{background: (activeTab === 'Alignment') ? '#ed9824' : ''}">
        <strong>{{getGene}}</strong> sequence: {{seqRes}} <br/>
        <strong>{{getStructure.pdb}}</strong> structure: {{structRes}}
      </span>

      <!-- Help Panel -->
      <span v-if="!helpOption" class="close" @click="closePanel"></span>
    </div>
</template>

<script>
import store from '../../store/index'
import * as panel3d from '../StructureViewer/helpers/joleculeAPI'
import { mapGetters } from 'vuex'
import { findPDBMember } from '../../utils/loadData'

export default {
  name: 'Title',
  data () {
    return {
      pdb: null,
      search: require('../../assets/img/search_100.png'),
      seqRes: null,
      structRes: null,
      helpOption: true,
      activeTab: null
    }
  },
  props: ['showHelpIcon'],
  computed: {
    data () {
      return {
        hash: store.state.hash
      }
    },
    scrim () {
      return store.state.showScrim
    },
    ...mapGetters([
      'getHash',
      'getOrganism',
      'getGene',
      'getStructure',
      'getVariantResidues',
      'getIntersectingStructs',
      'getVariantStructs'
    ])
  },
  watch: {
    showHelpIcon: function (bool) {
      this.helpOption = bool
      if (bool) {
        this.activeTab = null
      }
    },
    getStructure: function (newVal) {
      var urlParams = ''
      const query = store.state.queryStore
      if (query[newVal.accession]) {
        urlParams = decodeURIComponent(query[newVal.accession])
      }
      var _this = this
      if (this.getGene && this.getOrganism && this.getStructure && newVal) {
        history.pushState(null, document.title, '/' +
      _this.getOrganism + '/' + _this.getGene + '/' + _this.getStructure.pdb + '/' + _this.getStructure.chain + urlParams + '#' + _this.getHash)
      }
    },
    getOrganism: function (newVal) {
      var _this = this
      var urlParams = ''
      const query = store.state.queryStore
      if (query[_this.getStructure.accession]) {
        urlParams = query[_this.getStructure.accession]
      }
      if (this.getGene && newVal) {
        history.pushState(null, document.title, '/' +
      newVal + '/' + _this.getGene + '/' + _this.getStructure.pdb + '/' + _this.getStructure.chain + urlParams + '#' + _this.getHash)
      }
    },
    getGene: function (newVal) {
      var _this = this
      var urlParams = ''
      const query = store.state.queryStore
      if (query[_this.getStructure.accession]) {
        urlParams = query[_this.getStructure.accession]
      }
      if (this.getOrganism && newVal) {
        history.pushState(null, document.title, '/' +
      _this.getOrganism + '/' + newVal + '/' + _this.getStructure.pdb + '/' + _this.getStructure.chain + urlParams + '#' + _this.getHash)
      }
    }
    // scrim: function (newVal) {
    //   var _this = this
    //   if (!newVal) { // And error message is null
    //     _this.dismissPanel()
    //   }
    // }
  },
  methods: {
    showAllVarPopup: function () {
      if ((this.getIntersectingStructs && Object.keys(this.getIntersectingStructs).length > 0) || (this.getSortedKeys && Object.keys(this.getSortedKeys).length > 0) || (this.getVariantResidues && Object.keys(this.getVariantResidues).length > 0)) {
        console.log('Set allVarPopUpCoordinates: ')
        console.log(this.getVariantResidues)
        console.log(this.getIntersectingStructs)
        console.log(this.getVariantStructs)
        store.commit('setAllVariantsCoordinates', document.getElementById('struct').getBoundingClientRect())
      }
    },
    showHelp: function () {
      this.$parent.$parent.resetPopup()
      this.$store.commit('setScrim', true)
      store.commit('setHelpTitle', 'Aquaria Help')
      store.commit('setNewUserStatus', false)
    },
    makeActive: function (className) {
      this.activeTab = className
      this.$parent.$parent.resetPopup()
      this.$parent.$parent.showHelpIcon = false
      this.$store.commit('setScrim', true)
      this.$parent.$parent.dropFragment()
      this.$parent.$parent.showSequenceInfo = (className === 'Sequence') ? true : false // eslint-disable-line
      this.$parent.$parent.showAlignmentInfo = (className === 'Alignment') ? true : false // eslint-disable-line
      this.$parent.$parent.showStructureInfo = (className === 'Structure') ? true : false // eslint-disable-line
      this.$parent.$parent.appendHash(className)
      if (className === 'Alignment') {
        store.commit('setAlignment', panel3d.joleculeAlignment.copyToClipboard())
      }
      this.$store.commit('setAllVariantsCoordinates', 'hide')
    },
    closePanel: function () {
      this.$parent.$parent.showSequenceInfo = false
      this.$parent.$parent.showAlignmentInfo = false
      this.$parent.$parent.showStructureInfo = false
      this.$store.commit('setScrim', false)
      this.$parent.$parent.showHelpIcon = true
      this.$parent.$parent.dropFragment()
    },
    openPanel (panelName) {
      this.$parent.$parent['show' + panelName + 'Info'] = true
      this.$store.commit('setScrim', true)
      this.$parent.$parent.showHelpIcon = false
    }
  },
  mounted () {
    const hash = window.location.hash
    if (hash.includes('Structure') && !hash.includes('Structures')) {
      this.openPanel('Structure')
    }
    if (hash.includes('Alignment')) {
      this.openPanel('Alignment')
    }
    if (hash.includes('Sequence')) {
      this.openPanel('Sequence')
    }
  },
  updated () {
    var _this = this
    _this.primary_accession = _this.getGene
    var selectedRes = new MutationObserver(function () {
      if (document.querySelector('#threeDSpan-inner-jolecule-soup-display-canvas-wrapper-selection').style.display === 'none') {
        _this.seqRes = null
        _this.structRes = null
        // _this.alignment = null
        store.commit('setAlignment', '')
      } else {
        var PDBIndex = findPDBMember(_this.getStructure.pdb, _this.getStructure.chain)
        var currentMember = store.state.Matches.clusters[PDBIndex.cluster].members[PDBIndex.member]
        var residues = document.querySelector('#threeDSpan-inner-jolecule-soup-display-canvas-wrapper-selection').innerText.split('\n')
        var pdb = currentMember.pdb_id + '[' + currentMember.model + ']-' + currentMember.pdb_chain + ':'
        var accession = _this.getGene + ':'
        _this.seqRes = residues[1].split(accession)[1]
        _this.structRes = residues[0].split(pdb)[1]
      }
    })
    selectedRes.observe(document.querySelector('#threeDSpan-inner-jolecule-soup-display-canvas-wrapper-selection'), {
      attributes: true, childList: true, characterData: true
    })
  }
}
</script>

