<template>
    <PopupGeneric :key="'allVariants'" id='allVariants' :PopupPos="PopupPos" :parentRef="ref" :header2="popupTitle" :style="{display: PopupPos ? 'block' : 'none'}">
        <!-- div class='minimal' v-if="PopupPos && getPopupType === 'minimal'" -->
          <!-- i v-html="getPopupText"></i>
          <span :class="getTotalPdbCount > 0 ? 'hasStruct' : 'noStruct'" @click="getTotalPdbCount > 0 ? showTreeView($event, null, getPopupText.replace(/\D/g, '')) : ''"> {{ getTotalPdbCount }} </span-->
          <div id="content2">
            <div v-for='(aKey, index) in sortedKeys' v-bind:key='aKey'>
              <p class='aLine' v-if="dataToPrint[aKey] && dataToPrint[aKey].isShown">
                Structures with
                <span v-if="index > 0 && (getIntersectingStructs && getIntersectingStructs[index-1] && getIntersectingStructs[index-1].length > 0)">
                  above and
                </span>
                {{dataToPrint[aKey].oldAa}}{{aKey}}{{dataToPrint[aKey].newAa}}:
                <!-- span v-if="dataToPrint[aKey].isCurrPdbInVar" class="theRight_orange"  @click="showTreeView($event,dataToPrint[aKey].newAa,aKey)">
                  {{dataToPrint[aKey].totalStructs}}
                </span>
                <span v-if="!dataToPrint[aKey].isCurrPdbInVar" class="theRight">
                  {{dataToPrint[aKey].totalStructs}}
                </span -->

                <span v-if="getIntersectingStructs && getIntersectingStructs[index] && getIntersectingStructs[index].length" :class="dataToPrint[aKey].theClass" @click="showTreeView($event, getIntersectingStructs[index], sortedKeys, getIntersectingStructs[index].length)">
                  {{getIntersectingStructs[index].length}}
                </span>
                <span v-else class="theRight">
                  0
                </span>
              </p>
            </div>
            <div>
              {{ noMissenseVars }}
            </div>
            <div id='theHelpContent' style="visibility:hidden;height:0px;">
              <hr id='anAaHr2' class='anAaHr'>
              <p class="aLine"> Shows how many matching structures have the missense variants specified in the URL. </p>
              <p class="aLine"> The selected structure is based on the order in which variants are specified. </p>
            </div>
          </div>
        <!-- /div -->
    </PopupGeneric>
</template>

<script>
import PopupGeneric from './PopupGeneric.vue'
import { mapGetters } from 'vuex'
import store from '../../store/index'

export default {
  name: 'PopupAllVar',
  components: {
    PopupGeneric
  },
  data () {
    return {
      PopupPos: null,
      ref: 'allVariants',
      timeOutVar: null,
      timer: null,
      sortedKeys: [],
      oneAaCodes: ['A', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'K', 'L', 'M', 'N', 'P', 'Q', 'R', 'S', 'T', 'V', 'W', 'Y'],
      popupTitle: 'Structures with missense variants',
      pinned: false,
      dataToPrint: {},
      isComputed: false,
      noMissenseVars: '',
      isAnyCountSeen: false
    }
  },
  computed: {
    data () {
      return {
        variantStructs: store.state.variantStructs,
        variantResidues: store.state.variantResidues,
        popupTitle: store.state.popupTitle,
        popupText: store.state.popupText,
        residue: store.state.residue
      }
    },
    popupCoordinates () {
      return store.state.popupCoordinates
    },
    variantCoordinates () {
      return store.state.variantCoordinates
    },
    allVariantsCoordinates () {
      return store.state.allVariantsCoordinates
    },
    ...mapGetters([
      'getTotalPDBres',
      'getVariant',
      'getVariantStructs',
      'getPopupText',
      'getVariantIdx',
      'getIsDefaultFeature',
      'getPopupType',
      'getSortedKeys',
      'getIntersectingStructs',
      'getVariantStructs',
      'getVariantResidues',
      'getCurrentSequence',
      'getInitialSequence',
      'getInitPdb'
    ]),
    getTotalPdbCount () {
      return this.getTotalPDBres
    }
  },
  watch: {
    allVariantsCoordinates: function (newVal) {
      if (newVal === 'hide') {
        this.PopupPos = null
      } else if (newVal) {
        // store.commit('allVariantsCoordinates', null)
        this.PopupPos = newVal
        this.stoptimeout()
      } else {
        this.starttimeout()
      }
    },
    popupCoordinates: function (newVal) {
      if (newVal) {
        // store.commit('setVariantCoordinates', null)
        // this.PopupPos = newVal
        this.stoptimeout()
      } else {
        this.starttimeout()
      }
    },
    variantCoordinates: function (newVal) {
      if (newVal) {
        // this.PopupPos = null
      }
    },
    getVariantStructs: function (newVal) {
      console.log('Calling getVariantStructs')
      if (this.isComputed === false) {
        this.getSortedKeys1(newVal)
        console.log(this.dataToPrint)
      }
    },
    getIntersectingStructs: function (newVal) {
      console.log('Calling getIntersectingStructs')
      if (this.isComputed === false) {
        this.getSortedKeys1(newVal)
        console.log(this.dataToPrint)
      }
    },
    getSortedKeys: function (newVal) {
      console.log('Calling getSortedKeys')
      if (this.isComputed === false) {
        this.getSortedKeys1(newVal)
        console.log(this.dataToPrint)
      }
    },
    getVariantResidues: function (newVal) {
      console.log('Calling getVariantResidues')
      if (this.isComputed === false) {
        this.getSortedKeys1(newVal)
        console.log(this.dataToPrint)
      }
    }
  },
  methods: {
    showTreeView: function (event, listOfPdbs, sortedKeys, count) {
      this.$children[0].showTreeView(event, null, null, listOfPdbs, sortedKeys, count)
    },
    starttimeout: function () {
      this.$children[0].timeout(true)
    },
    stoptimeout: function () {
      this.$children[0].timeout(false)
    },
    getResInfo: function () {
      return this.$children[0].getResInfo()
    },
    /* showTreeView: function (event, aaCode) {
      this.$children[0].showTreeView(event, aaCode)
    }, */
    getSortedKeys1: function (newVal) {
      /* var theKeys = Object.keys(_this.getVariantResidues).sort(function (a, b) {
        return _this.getVariantResidues[a].order - _this.getVariantResidues[b].order
      })
      */
      var _this = this
      console.log('Calling getSortedKeys1')
      if (_this.getSortedKeys && _this.getVariantResidues && _this.getVariantStructs && _this.getIntersectingStructs && _this.isComputed === false) {
        _this.totalZeros = 0
        _this.counter_intersectingStructs = 0
        _this.getSortedKeys.forEach(function (key, keyI) {
          if (!key.match(/\-/) && _this.oneAaCodes.includes(String(_this.getVariantResidues[key].newAas[0][0]))) { // eslint-disable-line no-useless-escape
            if (!_this.sortedKeys.includes(key)) {
              _this.sortedKeys.push(key)
              _this.dataToPrint[key] = {}
              if (Object.hasOwnProperty.call(_this.getVariantResidues, key) && Object.hasOwnProperty.call(_this.getVariantResidues[key], 'newAas')) {
                _this.dataToPrint[key].newAa = String(_this.getVariantResidues[key].newAas[0][0])
              }
              _this.dataToPrint[key].oldAa = _this.getTheOldAa(key)
              // _this.dataToPrint[key].totalStructs = _this.structCounts(key, String(_this.dataToPrint[key].newAa), _this.counter_intersectingStructs)
              _this.dataToPrint[key].totalStructs = _this.structCounts2(keyI)
              _this.dataToPrint[key].theClass = _this.isInCurrStruct(key, String(_this.getVariantResidues[key].newAas[0][0]))
              _this.counter_intersectingStructs = _this.counter_intersectingStructs + 1
              _this.dataToPrint[key].isCurrPdbInVar = _this.isCurrPdbIncluded(key, String(_this.dataToPrint[key].newAa))

              if (_this.totalZeros === 1 && _this.isAnyCountSeen === true) {
                _this.dataToPrint[key].isShown = false
              } else if (_this.dataToPrint[key].totalStructs > 0) {
                _this.dataToPrint[key].isShown = true
                _this.totalZeros = 0
                _this.isAnyCountSeen = true
              } else if (_this.dataToPrint[key].totalStructs === 0 && _this.totalZeros === 0) {
                _this.totalZeros = 1
                _this.dataToPrint[key].isShown = true
              } else {
                _this.dataToPrint[key].isShown = false
              }
            }
          }
        })
        _this.isComputed = true
        console.log('These are the dataToPrint: ')
        console.log(_this.dataToPrint)
        this.noMissenseVars = ''
      } else {
        console.log('These data are not ok?')
        this.noMissenseVars = 'No missense variants specified.'
      }
    },
    getTheOldAa: function (theRes) {
      var _this = this
      if (Object.hasOwnProperty.call(_this.getVariantResidues, theRes) && Object.hasOwnProperty.call(_this.getVariantResidues[theRes], 'oldAa')) {
        if (_this.getVariantResidues[theRes].oldAa !== '') {
          return _this.getVariantResidues[theRes].oldAa
        } else if (_this.getInitialSequence) {
          return _this.getInitialSequence[parseInt(theRes) - 1]
        } else {
          return '?'
        }
      } else {
        return '?'
      }
    },
    structCounts2: function (idxIntersectingStructs) {
      if (this.getIntersectingStructs && Object.hasOwnProperty.call(this.getIntersectingStructs, idxIntersectingStructs)) {
        console.log('Found the key ' + this.getIntersectingStructs[idxIntersectingStructs].length)
        return this.getIntersectingStructs[idxIntersectingStructs].length
      } else {
        return 0
      }
    },
    isInCurrStruct: function (resPos, newAa) {
      var _this = this
      if (!_this.getInitPdb || _this.getInitPdb === '') {
        return 'theRight_orange'
      } else {
        // Check if in pdb list
        let isFound = false
        if (_this.getVariantStructs && Object.hasOwnProperty.call(_this.getVariantStructs, resPos) && Object.hasOwnProperty.call(_this.getVariantStructs[resPos], newAa) && Object.hasOwnProperty.call(_this.getVariantStructs[resPos][newAa], 'pdbs')) {
          _this.getVariantStructs[resPos][newAa].pdbs.forEach(function (aPdbAndChain) {
            if (aPdbAndChain.includes(_this.getInitPdb)) {
              isFound = true
            }
          })
          if (isFound === true) {
            return 'theRight_orange'
          } else {
            return 'theRight_gray'
          }
        } else {
          // else return default if len > 0
          return 'theRight_gray'
        }
      }
    },
    structCounts: function (theRes, theAa, idxIntersectingStructs) {
      console.log('testing123 ' + theRes + ' ' + theAa)
      console.log(this.getVariantStructs)

      if (this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, theRes) && Object.hasOwnProperty.call(this.getVariantStructs[theRes], theAa) && Object.hasOwnProperty.call(this.getVariantStructs[theRes][theAa], 'pdbs') && Object.keys(this.getIntersectingStructs).length > idxIntersectingStructs) {
        console.log('testing123 ' + theRes + ' ' + theAa + ' ' + this.getVariantStructs[theRes][theAa].pdbs.length)
        if (this.getIntersectingStructs[idxIntersectingStructs].length <= this.getVariantStructs[theRes][theAa].pdbs.length) {
          console.log('All good ')
        }
        return this.getIntersectingStructs[idxIntersectingStructs].length
      } else {
        return 0
      }
    },
    isCurrPdbIncluded: function (key, newAa) {
      var _this = this
      if (!_this.getStructure) {
        return false
      }

      var currPdbChain = _this.getStructure.pdb + ':' + _this.getStructure.chain
      if (_this.getVariantStructs && Object.hasOwnProperty.call(_this.getVariantStructs, key) && Object.hasOwnProperty.call(_this.getVariantStructs[key], newAa) && _this.getVariantStructs[key][newAa].pdbs.includes(currPdbChain)) {
        return true
      } else {
        return false
      }
    }
  }
}
</script>

<style>
.allVariants {
    cursor: pointer;
    color: var(--primary-label);
    padding: 5px;
}

#content2 {
    cursor: pointer;
    background-color: var(--popup-body);
    border-bottom-left-radius: 16px;
    border-bottom-right-radius: 16px;
    padding: 5px;
}

.aLine {
  margin:2px;
}

.theRight {
  float: right;
  padding-right: 5px;
}

.theRight_orange {
  float: right;
  padding-right: 4px;
  color: var(--text) !important;
  background-color: var(--primary-highlight);
  border-radius: 25%;
  padding-left: 4px;
  cursor: pointer;
}

.theRight_gray {
  float: right;
  padding-right: 4px;
  color: var(--text) !important;
  background-color: #999;
  border-radius: 25%;
  padding-left: 4px;
  cursor: pointer;
}

#theHelpContent {
  overflow: hidden;
  height: auto;
}

</style>
