<template>
    <div id="header">
        <div id="logo" class="item fix level1" @click="showAbout"><img v-bind:src="aquariasymbol" /></div>
        <Title :showHelpIcon='showHelpIcon'/>
        <Menu />
    </div>
</template>
<script>
import Title from './Title'
import Menu from './Menu'
export default {
  name: 'Header',
  components: {
    Title,
    Menu
  },
  data () {
    return {
      aquariasymbol: require('../../assets/images/aquaria-symbol-01.svg')
    }
  },
  props: ['showHelpIcon'],
  methods: {
    showAbout: function () {
      this.$parent.appendHash('About')
      this.$store.commit('setScrim', true)
      this.$parent.showAboutAquaria = true
      this.$parent.resetPopup()
    }
  }
}
</script>
