<template>
    <div id="scrim" v-if="show" @click="dismissPanel" :class="scrimClass" :style="{zIndex: loader ? 6 : scrimLevel}">
        <div id='waitingFrame' v-if="loader">
            <div id='waitinghtml'>
                <img :src='overlay' />
                <p id="appletMessage">{{loaderMsg}}</p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'Scrim',
  data () {
    return {
      overlay: require('../../assets/img/aquaria-loader3.svg')
    }
  },
  props: ['show'],
  computed: {
    loader () {
      return this.$store.state.Loader
    },
    loaderMsg () {
      return this.$store.state.LoaderMsg
    },
    scrimClass () {
      return 'level' + this.$store.state.scrimLevel
    },
    scrimLevel () {
      return this.$store.state.scrimLevel
    }
  },
  watch: {
    show (val) {
      if (!val) {
        this.dismissPanel()
      }
    },
    loader (bool) {
      const hash = window.location.hash
      // When loading state ends, is not a new user and does not have any panel fragments
      if (!bool && !this.$store.state.newUser && !hash.match(/[a-z A-Z]{3,}/)) {
        this.$store.commit('setScrim', false)
      }
    }
  },
  methods: {
    dismissPanel () {
      if (!document.querySelector('#waitingFrame')) {
        this.$parent.dropFragment()
      }
      if (this.$store.state.errorMsg) {
        document.querySelector('#errOk').click()
      } else {
        this.$parent.showSequenceInfo = false
        this.$parent.showAlignmentInfo = false
        this.$parent.showStructureInfo = false
        this.$parent.showAboutAquaria = false
        this.$store.commit('setScrim', false)
        this.$parent.showHelpIcon = true
        this.$parent.resetPopup()
        this.$store.commit('setBottomPanel', false)
        this.$store.commit('setHelpTitle', null)
        this.$store.commit('setNewUserStatus', false)
        this.$store.commit('setVariantCoordinates', null)
      }
    }
  }
}
</script>

<style type=text/css scoped>
#waitinghtml {
    background-color: rgba(0,0,0,0.7);
    pointer-events: none;
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-flow: column wrap;
    text-align: center;
}
p#appletMessage {
    color: white;
    text-align: center;
    margin: 2rem auto;
    height: 5rem;
}
#waitingFrame {
    overflow: hidden;
    width: 100vw;
    top: 0px;
    left: 0px;
    height: 100vh;
    position: fixed;
    z-index: 6;
    pointer-events: none;
    display: block;
}
img {
    width: 20vh;
    margin: auto;
    transform: translate(0, 10vh);
}
</style>
