<template>
  <div>
    <div class="tabs rel level6">
      <div class="tab"  id="Structures" v-on:click="renderClusters" @mouseenter="activateStructs = true" @mouseleave="activateStructs = false">
        <span class="icon"><img v-bind:src="MS" /></span>
        <span v-if="activateStructs || showStructures || showFeatures" class="panelTitle">
          &nbsp;&nbsp;Matching Structures&nbsp;&nbsp;
          <span class="counter" v-if="getStructCount > 0"> {{ getStructCount.toLocaleString() }}</span>
        </span>
      </div>
      <MatchingStructures v-bind:showPanel='showStructures'/>
      <div class="tab"  id="Features" v-on:click="renderFeatures" @mouseenter="activateFeats = true" @mouseleave="activateFeats = false">
        <span class="icon"><img v-bind:src="FT" /></span>
        <span v-if="activateFeats || showFeatures || showStructures" class="panelTitle">
          &nbsp;&nbsp;Features&nbsp;&nbsp;
          <span class="counter" id="featureCounter" v-if="getFeatCount">{{ getFeatCount.toLocaleString() }}</span>
        </span>
      </div>
      <Features v-bind:showPanel='showFeatures'/>
    </div>
    <!-- <div id="scrim" v-if="showStructures || showFeatures" @click="dismissPanel" class="level5"></div> -->
  </div>
</template>
<script>
import MatchingStructures from '../MatchingStructures/MatchingStructures'
import Features from '../Features/Features'
import { mapGetters } from 'vuex'

export default {
  name: 'Tabs',
  components: {
    MatchingStructures,
    Features
  },
  data () {
    return {
      MS: require('../../assets/img/MS-01.svg'),
      FT: require('../../assets/img/FT-01.svg'),
      activateStructs: false,
      activateFeats: false,
      showStructures: false,
      showFeatures: false
    }
  },
  computed: {
    ...mapGetters([
      'getFeatCount',
      'getStructCount',
      'getMatches',
      'getBottomPanel'
    ])
  },
  watch: {
    getMatches: function () {
      if (window.location.hash.toLowerCase().includes('structures')) {
        this.setState('Structures', 'Features')
      }
    },
    getBottomPanel (bool) {
      if (!bool) {
        this.dismissPanel()
      }
    }
  },
  methods: {
    renderClusters: function () {
      this.$store.commit('showLoader', false)
      this.$store.commit('setScrim', true)
      this.updateHash('Structures')
      this.setState('Structures', 'Features')
      this.$parent.resetPopup()
    },
    renderFeatures: function () {
      this.$store.commit('showLoader', false)
      this.$store.commit('setFeatCount', 0)
      this.$store.commit('setScrim', true)
      this.updateHash('Features')
      this.setState('Features', 'Structures')
      this.$parent.resetPopup()
    },
    updateHash: function (string) {
      this.$parent.appendHash(string)
    },
    setState: function (activePanel, inactivePanel) {
      this.$store.commit('setBottomPanel', activePanel)
      this['show' + activePanel] = true
      this['show' + inactivePanel] = false
      this.activateTab(activePanel)
      this.inactivateTab(inactivePanel)
    },
    dismissPanel: function () {
      this.showStructures = false
      this.showFeatures = false
      this.$parent.dropFragment()
      this.hideTabs()
    },
    activateTab: function (elem) {
      document.querySelector('#' + elem).style.display = 'block'
      document.querySelectorAll('.tab').forEach(element => {
        element.classList.remove('active')
      })
      document.querySelector('#' + elem).classList += ' active'
    },
    inactivateTab: function (elem) {
      document.querySelectorAll('.tab').forEach(element => {
        element.classList.remove('inactive')
      })
      document.querySelector('#' + elem).classList += ' inactive'
    },
    hideTabs: function () {
      document.querySelectorAll('.tab').forEach(element => {
        element.classList.remove('inactive')
      })
      document.querySelectorAll('.tab').forEach(element => {
        element.classList.remove('active')
      })
    }
  },
  mounted () {
    // Move this to Wrapper component and using props to update matching structures panel
    if (window.location.hash.toLowerCase().includes('features')) {
      this.setState('Features', 'Structures')
    }
  }
}
</script>
