<template>
    <PopupGeneric :key="'variant'" id='variant' :style="{display: PopupPos ? 'block' : 'none'}" :header="title" :PopupPos="PopupPos" :parentRef="ref">
      <div class='aaLightBg' v-if="PopupPos">
          <p id='spanDefaultDesc' v-if="defaultDesc"> {{defaultDesc}} </p>
          <div v-for='aaCode in oneAaCodes' v-bind:key='aaCode' :id="'divVarInfo_' + aaCode" :style="{display: (aaCode === variant) ? 'block' : 'none'}">
              <expandable-text-line use-click v-html="dataStr + '.'" v-for='dataStr in getAaDataAsArr(aaCode)' v-bind:key='dataStr'>
              </expandable-text-line>
              <i>Structures with {{getOldAa()}}{{getVarDisplay()}}{{aaCode}}:</i> <span :class='getIsShownVar(variant)' @click="showTreeView($event, aaCode)">{{getPdbCount(aaCode)}}</span>
          </div>
          <hr id='anAaHr' class='anAaHr' :hidden='isHrHidden()'>
          <div id='divPosInfo'>
              <span class="weight700" v-html='getResInfo()'></span><br>
              <!-- <span class="weight600" v-html='getResInfo'> Residue {{ getVariant }} </span><br> -->
              <span v-for='(arrObj, keyStr) in getPosInfoDataAsArr()' v-bind:key='keyStr'>
                  <expandable-text-line use-click>
                      <span v-html='keyStr'></span>:
                      <span v-for='dataStr in arrObj' v-bind:key="dataStr" v-html="dataStr  + '. '">
                      </span>
                  </expandable-text-line>
              </span>
              <i>Structures with residue {{ getVariant }}: </i>
              <span :class='getIsShownRes()' @click="showTreeView($event)">{{getTotalPdbCount}}</span>
          </div>
      </div>
      <div class="btnsDiv" v-if="PopupPos">
        <span>See also:</span> <span class='pAaColor'> {{getOldAa()}} &rarr; </span>
        <button v-for='aaCode in oneAaCodes' v-bind:key='aaCode' :id="'btnVarInfo_' + aaCode" :class="getIfBoldInfoPres(aaCode)" @click="highlightAa(aaCode)">{{aaCode}}</button>
      </div>
    </PopupGeneric>
</template>

<script>
import ExpandableTextLine from 'vue-expandable-text-line'
import PopupGeneric from './PopupGeneric.vue'
import store from '../../store/index'
import { mapGetters } from 'vuex'

export default {
  name: 'PopUpVariant',
  components: {
    PopupGeneric,
    ExpandableTextLine
  },
  data () {
    return {
      oneAaCodes: ['A', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'K', 'L', 'M', 'N', 'P', 'Q', 'R', 'S', 'T', 'V', 'W', 'Y'],
      threeAaCodes: ['Ala', 'Cys', 'Asp', 'Glu', 'Phe', 'Gly', 'His', 'Ile', 'Lys', 'Leu', 'Met', 'Asn', 'Pro', 'Gln', 'Arg', 'Ser', 'Thr', 'Val', 'Trp', 'Tyr'],
      variant: null,
      defaultDesc: null,
      count: 0,
      PopupPos: null,
      hasVariant: false,
      hasResidue: false,
      ref: 'variant',
      pinned: false,
      timer: null,
      title: null
    }
  },
  computed: {
    variantCoordinates () {
      return store.state.variantCoordinates
    },
    popupCoordinates () {
      return store.state.popupCoordinates
    },
    popupTitle () {
      return store.state.popupTitle
    },
    loader () {
      return store.state.Loader
    },
    ...mapGetters([
      'getTotalPDBres',
      'getVariant',
      'getVariantStructs',
      'getVariantResidues',
      'getVariantIdx',
      'getMatches',
      'getPopupTitle',
      'getStructure',
      'getIsDefaultFeature',
      'getCurrentSequence',
      'getInitialSequence'
    ]),
    getTotalPdbCount () {
      let totalNumStructs = 0
      for (let i = 0; i < this.oneAaCodes.length; i++) {
        if (this.getVariantStructs && this.getVariant && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], this.oneAaCodes[i]) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant][this.oneAaCodes[i]], 'pdbs')) {
          totalNumStructs = totalNumStructs + this.getVariantStructs[this.getVariant][this.oneAaCodes[i]].pdbs.length
        }
      }
      return totalNumStructs
    }
  },
  watch: {
    pinned: function (val) {
      if (val) {
        this.stoptimeout()
      } else {
        this.starttimeout()
      }
    },
    variantCoordinates: function (newVal) {
      var _this = this
      if (newVal) { // Hide popup when no coordinates passed
        if (typeof (newVal) !== 'object') {
          _this.PopupPos = null
        } else {
          store.commit('setPopupCoordinates', null)
          _this.PopupPos = newVal // Store residue coordinates to update popup position when popup body updated
          _this.$children[0].float = false
          _this.handleVariantPopup()
          _this.handlePin()
          if (!_this.loader) {
            _this.stoptimeout()
          }
        }
      } else {
        _this.starttimeout()
      }
    },
    popupCoordinates: function (newVal) {
      if (newVal && !this.pinned) {
        this.PopupPos = null
      }
    },
    popupTitle: function (value) {
      if (value) {
        this.title = value
      } else if (!value && !this.pinned) {
        this.title = value
        this.resetToInitVar()
      }
    }
  },
  methods: {
    handlePin: function () {
      const thePin = document.getElementById('loadedPin')
      if (thePin && thePin.classList.contains('loadedPin_active')) {
        thePin.classList.remove('loadedPin_active')
        thePin.classList.add('loadedPin')
        this.pinned = false
      }
    },
    starttimeout: function () {
      // this.$children[0].starttimeout()
      this.$children[0].timeout(true)
    },
    stoptimeout: function () {
      // this.$children[0].stoptimeout(this.starttimeout())
      this.$children[0].timeout(false)
    },
    showTreeView: function (event, aaCode) {
      this.$children[0].showTreeView(event, aaCode)
    },
    handleVariantPopup: function () {
      // Enable amino acid buttons; Show default variant text
      this.variant = this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx][0].toUpperCase()
      this.defaultDesc = this.getDefaultDesc()
      this.handleInitAa()
    },
    getOldAa: function () {
      console.log('this.getVariantResidues ... ')
      console.log(this.getVariantResidues)
      console.log('This is the saved variant ' + this.getVariant)
      console.log(this.getCurrentSequence)
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant)) {
        if (this.getVariantResidues[this.getVariant].oldAa !== '') {
          return this.getVariantResidues[this.getVariant].oldAa
        } else {
          return this.getInitialSequence.sequence[this.getVariant - 1]
        }
      }
    },
    highlightAa (aa) { // Highlight selected amino acid in buttons panel
      this.variant = aa.toUpperCase()
      const aaBtn = document.querySelector('#btnVarInfo_' + this.variant)
      const speRep = document.getElementById('varSpe_toRep')
      const newAa = document.querySelector('#newAa')
      const consInfo = document.getElementById('consInfo')
      if (document.querySelector('.selCol')) { document.querySelector('.selCol').classList.remove('selCol') }
      if (aaBtn) { aaBtn.classList += ' selCol' }
      if (speRep) {
        speRep.innerText = this.getNewAa(aa)
      }
      if (newAa) {
        newAa.innerText = this.getNewAa_opp(aa) // this.threeAaCodes[idx]
      }
      if (consInfo) {
        if (this.getOldAa() === aa) {
          consInfo.innerText = 'Synonymous'
        } else if (this.getVariantResidues && (this.getVariantIdx > -1 || this.pinned) && Object.hasOwnProperty.call(this.getVariantResidues, this.getVariantIdx)) {
          consInfo.innerText = this.getVariantResidues[this.getVariantIdx].consequent
        }
      }
      if (this.isHrHidden() === true && document.getElementById('anAaHr')) {
        document.getElementById('anAaHr').hidden = false
      }
      // console.log('Running initially? ' + ' ' + aa + ' ' + speRep)
    },
    getNewAa_opp: function (aa) {
      if (this.getVariantResidues && (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'isOne') && (this.getVariantIdx > -1 || this.pinned) && this.getVariantResidues[this.getVariant].isOne[this.getVariantIdx] === true)) {
        // specify as 3 letters.
        const idx = this.oneAaCodes.indexOf(aa)
        if (idx !== -1) {
          return this.threeAaCodes[idx]
        } else {
          return ''
        }
      } else {
        return aa
      }
    },
    getNewAa: function (aa) {
      if (this.getVariantResidues && (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'isOne') && (this.getVariantIdx > -1 || this.pinned) && this.getVariantResidues[this.getVariant].isOne[this.getVariantIdx] === true)) {
        // specify as 3 letters.
        return aa
      } else {
        const idx = this.oneAaCodes.indexOf(aa)
        if (idx !== -1) {
          return this.threeAaCodes[idx]
        } else {
          return ''
        }
      }
    },
    getIfBoldInfoPres: function (aa) {
      let isMakeBold = false
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], aa)) {
        for (let i = 0; i < this.getVariantResidues[this.getVariant][aa].length; i++) {
          if (/* this.getVariantResidues[this.getVariant][aa][i].match('UniProt') || this.getVariantResidues[this.getVariant][aa][i].match('FunVar') || this.getVariantResidues[this.getVariant][aa][i].match('COSMIC') || */ (this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], aa) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant][aa], 'pdbs') && this.getVariantStructs[this.getVariant][aa].pdbs.length > 0)) {
            isMakeBold = true
          }
        }
      }
      if (this.getVariantResidues && this.getVariant > 0 && (this.getVariantIdx > -1 || this.pinned || this.title)) {
        if (isMakeBold === true) {
          if (aa.toUpperCase().includes(this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx][0].toUpperCase())) {
            return 'btnAaBold_b selCol'
          } else {
            return 'btnAaBold_b'
          }
        } else {
          if (aa.toUpperCase().includes(this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx][0].toUpperCase())) {
            return 'btnAaBold selCol'
          } else {
            return 'btnAaBold'
          }
        }
      }
    },
    getAaDataAsArr: function (aa) {
      var arrForExpandable = []
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], aa)) {
        for (let i = 0; i < this.getVariantResidues[this.getVariant][aa].length; i++) {
        }
        return this.getVariantResidues[this.getVariant][aa]
      } else {
        return arrForExpandable
      }
    },
    resetToInitVar: function () {
      // console.log('Reset to init variant')

      // 2. Update consequenct str
      if (document.getElementById('consInfo') && Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'consequent') && (this.getVariantIdx > -1 || this.pinned)) {
        document.getElementById('consInfo').innerHTML = this.getVariantResidues[this.getVariant].consequent[this.getVariantIdx]
        console.log('Updated consInfo ... ')
      }
      // 3. Update div
      // 4. Update buttons at bottom & span_missense at top.
      this.handleInitAa()
      if (document.getElementById('spanDefaultDesc')) {
        document.getElementById('spanDefaultDesc').hidden = false
      }
      if (this.isHrHidden() === true && document.getElementById('anAaHr')) {
        document.getElementById('anAaHr').hidden = true
      }
      // 1. Update userSpecified feature str.
      if (document.getElementById('urlVariant') && Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'theOrigFeatStr') && (this.getVariantIdx > -1 || this.pinned)) {
        document.getElementById('urlVariant').innerHTML = this.getVariantResidues[this.getVariant].theOrigFeatStr[this.getVariantIdx]
        console.log('here.....???')
      }
    },
    getVarDisplay: function () {
      if (typeof this.getVariant === 'string' && this.getVariant.match('-')) {
        return this.getVariant.split('-')[0]
      } else {
        return this.getVariant
      }
    },
    getIsShownVar: function (aa) {
      if (this.getMatches && Object.hasOwnProperty.call(this.getMatches, 'Selected_PDB') && Object.hasOwnProperty.call(this.getMatches.Selected_PDB, 'pdb_id') && Object.hasOwnProperty.call(this.getMatches.Selected_PDB, 'pdb_chain')) {
        const key = this.getMatches.Selected_PDB.pdb_id + ':' + this.getMatches.Selected_PDB.pdb_chain

        if (this.getVariant && this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], aa) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant][aa], 'pdbs') && this.getVariantStructs[this.getVariant][aa].pdbs.includes(key)) {
          return 'selCol_padded'
        } else if (this.getVariant && this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], aa) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant][aa], 'pdbs') && !this.getVariantStructs[this.getVariant][aa].pdbs.includes(key)) {
          return 'noSelCol_padded'
        } else {
          return ''
        }
      }
    },
    getIsShownRes: function () {
      const arr1 = location.href.split('?')
      const arr2 = arr1[0].split('/')

      // console.log('This is ... ' + arr2[arr2.length - 2] + ':' + arr2[arr2.length - 1] + ' ' + this.variant + ' ' + this.getVariant)

      const thePdb = arr2[arr2.length - 2] + ':' + arr2[arr2.length - 1]

      if (this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant)) {
        const keys = Object.keys(this.getVariantStructs[this.getVariant])

        let isFound = false
        for (let i = 0; i < keys.length; i++) {
          if (Object.prototype.hasOwnProperty.call(this.getVariantStructs[this.getVariant][keys[i]], 'pdbs') && this.getVariantStructs[this.getVariant][keys[i]].pdbs.includes(thePdb)) {
            isFound = true
            return 'selCol_padded'
          }
        }

        if (isFound === false) {
          return 'noSelCol_padded'
        }
      }
      /*
      if (this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], 'isShown') && this.getVariantStructs[this.getVariant].isShown === true) {
        return 'selCol_padded'
      } else if ((this.getVariantStructs && this.getVariant > -1 && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], 'isShown') && this.getVariantStructs[this.getVariant].isShown === false) || this.getTotalPdbCount !== 0) {
        return 'noSelCol_padded'
      } else {
        return ''
      }
      */
    },
    getPdbCount: function (aa) {
      // console.log(store.getters.getVariant)
      // return 100// store.getters.getPdbCount(aa)
      if (this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], aa) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant][aa], 'pdbs')) {
        return this.getVariantStructs[this.getVariant][aa].pdbs.length
      }
      return 0
    },
    isHrHidden: function () {
      const defaultDesc = this.getDefaultDesc()
      // const divPopupTextVar = document.getElementById('popupText_variant')
      let isAnAa = false
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'newAas') && (this.getVariantIdx > -1 || this.pinned) && this.oneAaCodes.includes(this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx][0].toUpperCase())) {
        isAnAa = true
      }

      if (defaultDesc === '' && isAnAa === false) {
        // console.log('Num children count is: ') // + // divPopupTextVar.children.length)
        return true
      } else {
        return false
      }
    },
    getDefaultDesc: function () {
      // console.log('Restaurant ' + this.getVariantIdx + ' ' + this.getVariantResidues[this.getVariant].defaultDesc[this.getVariantIdx])

      if (this.getVariant && this.getVariantResidues && Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'defaultDesc') && (this.getVariantIdx > -1 || this.pinned || this.title)) {
        return this.getVariantResidues[this.getVariant].defaultDesc[this.getVariantIdx]
      } else {
        return ''
      }
    },
    getPosInfoDataAsArr: function () {
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'positionInfo')) {
        return this.getVariantResidues[this.getVariant].positionInfo
      }
      return []
    },
    handleInitAa: function () {
      var _this = this
      console.log('handleInitAa runs ')//  + this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx])
      console.log(this.getVariantIdx)
      console.log(this.getVariant)
      console.log(this.getVariantResidues)
      // console.log('handleInitAa runs (2) ' + this.getVariantResidues[this.getVariant].newAas + ' ' + this.getVariantIdx)
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'newAas') && (this.getVariantIdx > -1 || this.pinned) && this.oneAaCodes.includes(this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx][0].toUpperCase())) {
        _this.highlightAa(this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx][0].toUpperCase())
        // const aaBtn = document.getElementById('btnVarInfo_' + this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx][0].toUpperCase())
        // aaBtn.click()
      } else { // Hide all the divs
        const oldAa = this.getOldAa()
        for (let i = 0; i < this.oneAaCodes.length; i++) {
          const theDiv = document.getElementById('divVarInfo_' + this.oneAaCodes[i])
          if (typeof (theDiv) !== 'undefined' && theDiv !== null) {
            document.getElementById('divVarInfo_' + this.oneAaCodes[i]).hidden = true
            document.getElementById('btnVarInfo_' + this.oneAaCodes[i]).classList.remove('selCol')
          }
          // Hide oldAa btn
          if (oldAa === this.oneAaCodes[i]) {
            // document.getElementById('btnVarInfo_' + this.oneAaCodes[i]).hidden = true
          }
        }
        _this.updateSpanAa('')
      }
    },
    updateSpanAa: function (valToDisp) {
      console.log('Value to display is ' + valToDisp + this.getVariantIdx)
      if (document.getElementById('span_missenseHeading')) {
        /*
        let a = document.getElementById('span_missenseHeading').innerHTML
        a = a.replace(/[A-Z]*\)$/, valToDisp + ')')
        document.getElementById('span_missenseHeading').innerHTML = a
        */

        if (this.getVariantResidues && (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'isOne') && (this.getVariantIdx > -1 || this.pinned) && this.getVariantResidues[this.getVariant].isOne[this.getVariantIdx] === true)) {
          // specify as 3 letters.
          const idx = this.oneAaCodes.indexOf(valToDisp)
          if (idx !== -1) {
            document.getElementById('newAa').innerHTML = this.threeAaCodes[idx]
          } else {
            document.getElementById('newAa').innerHTML = ''
          }
        } else {
          document.getElementById('newAa').innerHTML = valToDisp
        }
        // console.log('the heading is updating here ' + valToDisp)
      }
    },

    getResInfo: function () {
      return this.$children[0].getResInfo()
    }
  }
}
</script>

<style>
.topTail {
  transform: rotate(180deg);
}
.aaLightBg {
    padding: 7px;
    cursor: pointer;
    color: #666666;
    background-color: var(--popup-body);
}
.btnsDiv {
    display: block;
    cursor: move;
    font-size: 14px;
    color: var(--primary-label) !important;
    background: #acacacd4;
    padding: 6px;
    border-bottom-left-radius: 16px;
    border-bottom-right-radius: 16px;
}

.btnsDiv > span:nth-of-type(1) {
    font-weight: 600 !important;
    font-size: 15px !important;
    color: var(--dark-text) !important;
}
.pAaColor {
    display: inline;
    color: var(--dark-text) !important;
    cursor: move; /* fallback if grab cursor is unsupported */
}
.btnAaBold {
    border: none;
    /* background-color: inherit; */
    cursor: pointer;
    color: var(--dark-text) !important;
    padding: 0px 4px;
    display: inline;
    font-weight: 200;
}
.btnAaBold_b {
    border: none;
    /* background-color: inherit; */
    cursor: pointer;
    color: var(--dark-text) !important;
    padding: 0px 4px;
    display: inline;
    font-weight: 800;
}
button {
  background: #b2b2b221;
}
.btnAaBold:hover {
    background-color: var(--primary-link);
}

.btnAaBold_b:hover {
    background-color: var(--primary-link);
    font-weight: bold;
}
.selCol {
    color: var(--text) !important;
    background-color: var(--primary-highlight);
    border-radius: 25%;
}
.selCol_padded {
  background-color: orange;
}
.noSelCol_padded {
  background-color: #999;
}
.noSelCol_padded, .selCol_padded {
  color: var(--text) !important;
  border-radius: 25%;
  padding-right: 4px;
  margin-left: 4px;
  padding-left: 4px;
}

.selCol_padded:hover {
    background-color: var(--primary-link);
}

.noSelCol_padded:hover {
    background-color: var(--primary-link);
}
.weight700{
  font-weight: 700!important;
  font-size: 12px!important;
}
</style>
